<template>
  <div class="card">
    <div class="card-header pb-0">
      <h6></h6>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0">
        <table class="table align-items-center table-hover  mb-0 ">
          <thead>
            <tr>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Plate-Number</th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Engine</th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">speed(km./hr.)</th>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Fuel</th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">temp</th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Updated Date
              </th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Playback</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="vehicle in vehicles" :key="vehicle._id" :class="{ 'expired': isExpired(vehicle.expired_date) }">
            
              <td>
                <div class="d-flex px-2 py-1" >
                  <div>
                    <img :src="getVehicleImage(vehicle)" class="avatar avatar-sm me-3" :alt="vehicle.name" />
                  </div>
                  <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm" @click="selectVehicle(vehicle)"><a href="#">{{ vehicle.number }} {{ vehicle.province }}</a></h6>
                    <p class="text-xs text-secondary mb-0">{{ vehicle.thai_address }}</p>
                  </div>
                </div>
              </td>
              <td class="align-middle text-center text-sm align-middle text-center position-relative">
                <span class="badge"
                  :class="{ 'bg-gradient-success': vehicle.engine === 'on', 'bg-gradient-secondary': vehicle.engine === 'off' }">{{
                    vehicle.engine }}</span>
                <div class="overlay-message" v-if="isExpired(vehicle.expired_date)"> </div>
              </td>
              <td class="align-middle text-center">
                <span class="text-secondary text-xs font-weight-bold">{{ vehicle.speed }}</span>
              </td>
              <td class="align-middle text-center" >
                <div class="d-flex align-items-center justify-content-center">
                  <span class="me-2 text-xs font-weight-bold">{{ parseFloat(vehicle.percent_fuel).toFixed(2) }}</span>
                  <div>
                    <div class="progress" style="max-width: 30px;">
                      <div class="progress-bar bg-gradient-info" role="progressbar" aria-valuenow="60" aria-valuemin="0"
                        aria-valuemax="100" :style="{ width: parseIntFuel(vehicle) + '%' }"></div>
                    </div>
                  </div>
                </div>
              </td>
              <td class="align-middle text-center">
                <span class="text-secondary text-xs font-weight-bold">{{ vehicle.tempsensor }}</span>
              </td>

              <td class="align-middle text-center" >
                <span class="text-secondary text-xs font-weight-bold">{{ vehicle.updated_date }}</span>
              </td>
              <td class="align-middle  text-center">
                <a href="javascript:;" class="text-secondary font-weight-bold text-xs" @click="startPlayback(vehicle)"
                  data-toggle="tooltip" data-original-title="Playback"></a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "VehiclesTable",
  props: {
    vehicles: {
      type: Array,
      required: true
    }
  },
  methods: {
    // Emit event when the "playback" link is clicked
    startPlayback(vehicle) {
      console.log('playback',vehicle)
      this.$emit("start-playback", vehicle);
    },
    // Emit event when a row is clicked
    selectVehicle(vehicle) {
      console.log('Table', vehicle)
      this.$emit("vehicle-selected", vehicle);
    },
    parseIntFuel(vehicle) {
      // Parse fuel percentage to integer
      return parseInt(vehicle.percent_fuel);
    },
    isExpired(expiredDate) {
      const now = new Date();
      return new Date(expiredDate) < now;
    },
    getVehicleImage(vehicle) {
      if (vehicle.engine === 'off') {
        return require('@/assets/img/truck-gray.png'); // Use gray truck image if engine is off
      } else {
        return require('@/assets/img/truck.png');; // Use regular truck image
      }
    }
  }

};
</script>

<style>
.expired {
    background-color: rgba(128, 128, 128, 0.5); /* Gray with 50% opacity */
    /* You can adjust the opacity level as per your requirement */
  }

  .overlay-message {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    color: white; /* Change text color as needed */
    font-weight: bold;
    padding: 5px; /* Adjust padding as needed */
    background-color: rgba(102, 101, 101, 0.7); /* Background color for overlay message */
  }
</style>

