// src/api/index.js
import axios from 'axios';
import store from '../store';
const baseURL = 'http://www.gpsok123.com:8000'; // Replace with your actual server URL

const api = axios.create({
    baseURL,
    headers: {
        'accept': 'application/json',
        'Content-Type': 'application/json',
    },
});

export const loginUser = (userData) => {
    // console.log(userData)
    // const token = store.getters.getToken;
    return api.post(`/api/v2/user/user/login/`, userData, {
        headers: {
            Authorization: userData,
        },
    });
};
export const getAllVehicle = () => {
    const token = store.getters.getToken;
    const username = store.getters.getUser;
    // console.log(username, token)
    return api.get(`/api/v2/process/newest_data_by_role/?access_name=${username}`, {
        headers: {
            'accessheader': token,
        },
    });
};
