<template>
  <div class="card">
    <div class="card-header pb-0">
      <h6></h6>
    </div>
    <div class="card-body px-2 pt-2 pb-2">
      <div ref="map" style="height: 600px;"></div>
    </div>
  </div>
</template>

<script>
import L from "leaflet"; // Import Leaflet library
import "leaflet/dist/leaflet.css"; // Import Leaflet CSS
import 'leaflet-plugin-trackplayback/dist/leaflet.trackplayback.js'; // Import
import 'leaflet-plugin-trackplayback/dist/control.trackplayback.js';
import 'leaflet-plugin-trackplayback//dist/control.playback.css';
// import '@/assets/css/control.playback.css';
import testData from "@/assets/test.json"; // Import test.json file

export default {
  name: "MapPlayback",
  components: {

  },
  props: {
    vehicles: {
      type: Array,
      required: true
    },
    selectedVehicle: {
      type: Object, // Type of selected vehicle
      default: null // Default value
    }
  },
  data() {
    return {
      map: null, // Variable to hold the Leaflet map object
      customMarkerIcon: L.icon({
        iconUrl: require("@/assets/img/truck.png"), // Path to custom marker image
        iconSize: [50, 50], // Size of the custom marker icon
        iconAnchor: [15, 30], // Anchor point of the custom marker icon
      }),
      markers: [] // Array to store marker references
    };
  },
  mounted() {
    this.initMap(); // Call function to initialize map after component is mounted
  },
  methods: {

    initMap() {
      // Check if testData has at least one object
      if (testData.length > 0) {
        const firstObject = testData[0]; // Get the first object from testData
        const initialCoords = [firstObject.lat, firstObject.lng]; // Extract coordinates
        this.map = L.map(this.$refs.map).setView(initialCoords, 20); // Set initial view
      } else {
        // Fallback coordinates if testData is empty
        this.map = L.map(this.$refs.map).setView([13.736717, 100.523186], 20);
      }

      // Add OpenStreetMap tile layer
      L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        attribution: " OpenStreetMap contributors",
      }).addTo(this.map);

      // const data = [{ lat: 30, lng: 116, time: 1502529980, dir: 320, info: [{ key: 'name', value: 'ship1' }] }]
      // console.log(testData);
      // const trackplayback = L.trackplayback(testData, this.map)

      const trackplayback = L.trackplayback(testData, this.map, {
        trackLineOptions: {
          // whether draw track line
          isDraw: true,
          stroke: true,
          color: '#1C54E2',
          weight: 4,
          fill: false,
          fillColor: '#000',
          opacity: 0.8
        },
        targetOptions: {
          useImg: true,
          imgUrl: require("@/assets/img/truck.png"),
          width: 50,
          height: 50,
        },

      });
      const trackplaybackControl = L.trackplaybackcontrol(trackplayback, {
        position: 'bottomright',
        // showOptions: false,
        // showInfo: false,
        // showSlider: true,
        // autoPlay: true
      });
      trackplaybackControl.addTo(this.map);
      // trackplayback.start()
      // trackplayback.showTrackPoint()()
      // trigger on time change
      trackplayback.on('tick', e => {
        console.log(e)
        const timeExists = testData.some(data => data.time === e.time);
        if (timeExists) {
          // Emit custom event with the tick time
          console.log(e)
          this.$emit('tick-vehicle', e.time);
        }
      }, this)
    },
    addMarkers() {
      this.markers = []; // Clear existing markers
      this.vehicles.forEach(vehicle => {
        const marker = L.marker([vehicle.latitude, vehicle.longitude], {
          icon: this.customMarkerIcon
        }).addTo(this.map);
        marker.bindPopup(`
      <div>
        <table>
          <tr>
            <td>Company:</td>
            <td>${vehicle.company}</td>
          </tr>
          <tr>
            <td>Name:</td>
            <td>${vehicle.name}</td>
          </tr>
          <tr>
            <td>Number:</td>
            <td>${vehicle.number}</td>
          </tr>
          <tr>
            <td>Province:</td>
            <td>${vehicle.province}</td>
          </tr>
          <tr>
            <td>Thai Address:</td>
            <td>${vehicle.thai_address}</td>
          </tr>
          <tr>
            <td>Brand:</td>
            <td>${vehicle.brand}</td>
          </tr>
          <tr>
            <td>Driver First Name:</td>
            <td>${vehicle.driver_first_name}</td>
          </tr>
          <tr>
            <td>Driver Last Name:</td>
            <td>${vehicle.driver_last_name}</td>
          </tr>
          <tr>
            <td>Driver License:</td>
            <td>${vehicle.driver_license}</td>
          </tr>
          <tr>
            <td>Speed:</td>
            <td>${vehicle.speed}</td>
          </tr>
          <tr>
            <td>Temperature Sensor:</td>
            <td>${vehicle.tempsensor}</td>
          </tr>
          <tr>
            <td>Engine:</td>
            <td>${vehicle.engine}</td>
          </tr>
          <tr>
            <td>Fuel (Liters):</td>
            <td>${vehicle.fuel_liter}</td>
          </tr>
          <tr>
            <td>Fuel (%):</td>
            <td>${parseFloat(vehicle.percent_fuel).toFixed(2)}%</td>
          </tr>
        </table>
      </div>
    `);
        this.markers.push(marker); // Push marker reference to array
      });
    },

    focusOnVehicle(vehicle) {
      if (vehicle) {
        const marker = this.markers.find(marker => marker.getLatLng().equals([vehicle.latitude, vehicle.longitude]));
        if (marker) {
          this.map.setView(marker.getLatLng(), 20); // Zoom to selected marker
        }
      }
    }

  },
  watch: {
    vehicles: {
      handler() {
        // Call addMarkers when vehicles data changes
        // this.addMarkers();
        // this.initMap() 
      },
      deep: true
    },
    selectedVehicle(newValue) {
      console.log('MT', newValue)
      // Watch for changes in selected vehicle
      if (newValue !== null) {
        // Focus on the selected vehicle marker
        this.focusOnVehicle(newValue);
      }
    }
  }
};
</script>

