<!-- src\views\components\VehiclesTable.vue -->
<template>
  <div class="card">
    <div class="card-header pb-0">
      <h6></h6>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0">
        <table class="table align-items-center table-hover  mb-0 ">
          <thead>
            <tr>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Plate-Number</th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Engine</th>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Fuel</th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Updated Date
              </th>
              <th class="text-secondary opacity-7"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="vehicle in vehicles" :key="vehicle._id" >
              <td>
                <div class="d-flex px-2 py-1">
                  <div>
                    <img src="../../assets/img/truck.png" class="avatar avatar-sm me-3" :alt="vehicle.name" />
                  </div>
                  <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm" @click="selectVehicle(vehicle)"><a href="#">{{ vehicle.number }} {{ vehicle.province }}</a></h6>
                    <p class="text-xs text-secondary mb-0">{{ vehicle.thai_address }}</p>
                  </div>
                </div>
              </td>
              <td class="align-middle text-center text-sm">
                <span class="badge"
                  :class="{ 'bg-gradient-success': vehicle.engine === 'on', 'bg-gradient-secondary': vehicle.engine === 'off' }">{{
                    vehicle.engine }}</span>
              </td>
              <td class="align-middle text-center">
                <div class="d-flex align-items-center justify-content-center">
                  <span class="me-2 text-xs font-weight-bold">{{ parseFloat(vehicle.percent_fuel).toFixed(2) }}</span>
                  <div>
                    <div class="progress">
                      <div class="progress-bar bg-gradient-info" role="progressbar" aria-valuenow="60" aria-valuemin="0"
                        aria-valuemax="100" :style="{ width: parseIntFuel(vehicle) + '%' }"></div>
                    </div>
                  </div>
                </div>
              </td>


              <td class="align-middle text-center">
                <span class="text-secondary text-xs font-weight-bold">{{ vehicle.updated_date }}</span>
              </td>
              <!-- <td class="align-middle">
                <a href="javascript:;" class="text-secondary font-weight-bold text-xs" @click="startPlayback(vehicle)"
                  data-toggle="tooltip" data-original-title="Playback">playback</a>
              </td> -->
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "VehiclesTable",
  props: {
    vehicles: {
      type: Array,
      required: true
    }
  },
  methods: {
    // Emit event when the "playback" link is clicked
    startPlayback(vehicle) {
      console.log('playback',vehicle)
      this.$emit("start-playback", vehicle);
    },
    // Emit event when a row is clicked
    selectVehicle(vehicle) {
      console.log('Table', vehicle)
      this.$emit("vehicle-selected", vehicle);
    },
    parseIntFuel(vehicle) {
      // Parse fuel percentage to integer
      return parseInt(vehicle.percent_fuel);
    }
  }

};
</script>

