<template>
  <div class="py-4 container-fluid">
    <div class="row">
    <div class="col-8">
      <MapPlayback :vehicles="vehicles" :selectedVehicle="selectedVehicle"/>
    </div>
    <div class="col-4">
      <VehiclesPlaybackTable :vehicles="vehicles"  @vehicle-selected="selectVehicle" />
    </div>
  </div>
    
    <div class="mt-4 row">
      <div class="col-12">
        <!-- <ProjectsTable /> -->
      </div>
    </div>
  </div>
</template>

<script>
import MapPlayback from "./components/MapPlayback.vue";
import VehiclesPlaybackTable from "./components/VehiclesPlaybackTable.vue";
import { getAllVehicle } from "@/api";
import testData from "@/assets/test.json"; // Import test.json file
export default {
  name: "Playback",
  components: {
    // ProjectsTable,
    MapPlayback,
    VehiclesPlaybackTable,
  },
  data() {
    return {
      vehicles: [], // Initialize vehicles array to store fetched data
      selectedVehicle: null // Initialize selected vehicle to null
    };
  },
  async created() {
    try {
      // Fetch vehicle data
      // Fetch vehicle data based on route parameter
      const vehicleId = this.$route.params.vehicleId;
      console.log(vehicleId)
      const response = await getAllVehicle();
      // this.vehicles = response.data; // Store fetched vehicles data
      console.log('data',response.data)
      this.vehicles = testData;
    } catch (error) {
      console.error("Error fetching vehicle data:", error);
    }
  },
  methods: {
    // Method to handle vehicle selection
    selectVehicle(vehicle) {
      console.log('Map',vehicle);
      this.selectedVehicle = vehicle;
    }
  }
  
};
</script>
