  created() {
    this.fetchVehicles(); // Fetch vehicles when component is created
  },
  methods: {
    // Method to fetch vehicles
    async fetchVehicles() {
      try {
        const response = await getAllVehicle();
        this.vehicles = response.data; // Set fetched vehicles to component data
        if (this.vehicles.length > 0) {
          this.selectedVehicle = this.vehicles[0]; // Select the first item in vehicles array
        }
      } catch (error) {
        console.error("Error fetching vehicles:", error);
        // Handle error if necessary
      }
    },
    // Method to handle search button click
