<!-- src\views\Map.vue -->
<template>
  <div class="py-4 container-fluid">
    <div class="row">
    <div class="col-9">
      <MapTransporter :vehicles="vehicles" :selectedVehicle="selectedVehicle"/>
    </div>
    <div class="col-3">
      <VehiclesTable :vehicles="vehicles"  @vehicle-selected="selectVehicle" @start-playback="startPlayback" />
    </div>
  </div>
    
    <div class="mt-4 row">
      <div class="col-12">
        <!-- <ProjectsTable /> -->
      </div>
    </div>
  </div>
</template>

<script>
import MapTransporter from "./components/MapTransporter.vue";
import VehiclesTable from "./components/VehiclesTable.vue";
import { getAllVehicle } from "@/api";

export default {
  name: "Map",
  components: {
    // ProjectsTable,
    MapTransporter,
    VehiclesTable,
  },
  data() {
    return {
      vehicles: [], // Initialize vehicles array to store fetched data
      selectedVehicle: null // Initialize selected vehicle to null
    };
  },
  async created() {
    try {
      // Fetch vehicle data
      const response = await getAllVehicle();
      this.vehicles = response.data; // Store fetched vehicles data
      console.log('data',response.data)
    } catch (error) {
      console.error("Error fetching vehicle data:", error);
    }
  },
  methods: {

    // Method to handle vehicle selection
    selectVehicle(vehicle) {
      console.log('Map',vehicle);
      this.selectedVehicle = vehicle;
    },
    // Method to handle starting playback
    startPlayback(vehicle) {
      this.selectedVehicle = vehicle;
      // Redirect to VehiclePlayback route with selected vehicle ID
      // this.$router.push({ name: 'Playback', params: { vehicleId: vehicle._id } });
      window.open(`/playback/${vehicle._id}`, '_blank');
    }
  }
  
};
</script>
