  <!-- <div class="container top-0 position-sticky z-index-sticky">
    <div class="row">
      <div class="col-12">
        <navbar isBlur="blur  border-radius-lg my-3 py-2 start-0 end-0 mx-4 shadow" v-bind:darkMode="true"
          isBtn="bg-gradient-success" />
      </div>
    </div>
  </div> -->
  <main class="mt-0 main-content">
    <section>
      <div class="page-header min-vh-100">
        <div class="container">
          <div class="row">
            <div class="mx-auto col-xl-4 col-lg-5 col-md-7 d-flex flex-column mx-lg-0">
              <div class="card card-plain">
                <div class="pb-0 card-header text-start">
                  <h4 class="font-weight-bolder">Sign In</h4>
                  <p class="mb-0">Enter your username and password to sign in</p>
                </div>
                <div class="card-body">
                  <div role="form">
                    <div class="mb-3">
                      <input type="text" placeholder="Username" v-model="username" size="lg" class="form-control mb-3" />
                    </div>
                    <div class="mb-3">
                      <input type="password" placeholder="Password" v-model="password" name="password" size="lg"
                        class="form-control mb-3" />
                    </div>
                    <!-- <argon-switch id="rememberMe">Remember me</argon-switch> -->
                    <!-- ArgonAlert component -->
                    <argon-alert :color="alertColor" :dismissible="true" v-if="alertMessage">{{ alertMessage
                    }}</argon-alert>
                    <div class="text-center">
                      <argon-button class="mt-4" variant="gradient" color="success" fullWidth size="lg"
                        @click="signIn()">Sign in</argon-button>
                    </div>
                  </div>
                </div>
                <!-- <div class="px-1 pt-0 text-center card-footer px-lg-2">
                  <p class="mx-auto mb-4 text-sm">
                    Don't have an account?
                    <a href="javascript:;" class="text-success text-gradient font-weight-bold">Sign up</a>
                  </p>
                </div> -->
              </div>
            </div>
            <div
              class="top-0 my-auto text-center col-6 d-lg-flex d-none h-100 pe-0 position-absolute end-0 justify-content-center flex-column">
              <div
                class="position-relative bg-gradient-primary h-100 m-3 px-7 border-radius-lg d-flex flex-column justify-content-center overflow-hidden"
                style="background-image: url('https://raw.githubusercontent.com/creativetimofficial/public-assets/master/argon-dashboard-pro/assets/img/signin-ill.jpg');
          background-size: cover;">
                <span class="mask bg-gradient-success opacity-6"></span>
                <h4 class="mt-5 text-white font-weight-bolder position-relative">"GPS Tracking System"</h4>
                <p class="text-white position-relative"></p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
// import Navbar from "@/examples/PageLayout/Navbar.vue";
// import ArgonInput from "@/components/ArgonInput.vue";
// import ArgonSwitch from "@/components/ArgonSwitch.vue";
import ArgonButton from "@/components/ArgonButton.vue";
import ArgonAlert from "@/components/ArgonAlert.vue";
import { loginUser } from "@/api";
const body = document.getElementsByTagName("body")[0];

export default {
  name: "signin",
  components: {
    // Navbar,
    // ArgonInput,
    // ArgonSwitch,
    ArgonButton,
    ArgonAlert,
  },
  data() {
    return {
      username: "", // Changed from email to username
      password: "",
      error: "",
      alertMessage: "", // New data property for holding alert message
      alertColor: "success" // Default alert color
    };
  },
  methods: {
    async signIn() {
      try {
        const response = await loginUser({
          username: this.username, // Changed from email to username
          password: this.password,
        });
        // console.log(this.username);
        this.$store.dispatch("setToken", response.data.access_token);
        this.$store.dispatch("setUser", this.username);
        this.$store.dispatch("setAuthentication", true);
        // console.log('isAuthen ', this.$store.getters.isAuthenticated)
        // console.log("Login successful:", response.data);

        // Set success message for the alert
        this.alertMessage = "Login successful";
        this.alertColor = "success";
        this.$router.push("/map-realtime"); // Redirect to the dashboard
      } catch (error) {
        console.error("Login failed:", error.response.data);
        // Set error message for the alert
        this.alertMessage = error.response.data.detail;
        this.alertColor = "danger";
      }
    },
  },
  created() {
    this.$store.state.hideConfigButton = true;
    this.$store.state.showNavbar = false;
    this.$store.state.showSidenav = false;
    this.$store.state.showFooter = false;
    body.classList.remove("bg-gray-100");
  },
  beforeUnmount() {
    this.$store.state.hideConfigButton = false;
    this.$store.state.showNavbar = true;
    this.$store.state.showSidenav = true;
    this.$store.state.showFooter = true;
    body.classList.add("bg-gray-100");
  },
};
</script>
