// src\router\index.js
import { createRouter, createWebHistory } from "vue-router";
import Dashboard from "../views/Dashboard.vue";
import Tables from "../views/Tables.vue";
import Billing from "../views/Billing.vue";
import VirtualReality from "../views/VirtualReality.vue";
import RTL from "../views/Rtl.vue";
import Profile from "../views/Profile.vue";
import Signup from "../views/Signup.vue";
import Signin from "../views/Signin.vue";
import Map from "../views/Map.vue";
import Playback from "../views/Playback.vue";
import Report from "../views/Report.vue";
import store from "@/store";
const routes = [{
        path: "/",
        name: "/",
        redirect: "/map-realtime",
    },
    {
        path: "/map-realtime",
        name: "map-realtime",
        component: Map,
        meta: { requiresAuth: true }
    },
    {
        path: "/playback",
        name: "Playback",
        component: Playback,
        meta: { requiresAuth: true }
    },
    {
        path: "/report",
        name: "Report",
        component: Report,
        meta: { requiresAuth: true }
    },
    {
        path: "/dashboard-default",
        name: "Dashboard",
        component: Dashboard,
    },
    {
        path: "/tables",
        name: "Tables",
        component: Tables,
    },
    {
        path: "/billing",
        name: "Billing",
        component: Billing,
    },
    {
        path: "/virtual-reality",
        name: "Virtual Reality",
        component: VirtualReality,
    },
    {
        path: "/rtl-page",
        name: "RTL",
        component: RTL,
    },
    {
        path: "/profile",
        name: "Profile",
        component: Profile,
    },
    {
        path: "/signin",
        name: "Signin",
        component: Signin,
    },
    {
        path: "/signup",
        name: "Signup",
        component: Signup,
    },

];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes,
    linkActiveClass: "active",
});
router.beforeEach((to, from, next) => {
    if (to.meta.requiresAuth) { // Check if route requires authentication
        const isAuthenticated = store.getters.isAuthenticated; // Get authentication status from Vuex store
        if (isAuthenticated) {
            // User is authenticated, proceed to the requested route
            next();
        } else {
            // User is not authenticated, redirect to signin page or any other desired route
            next({ name: 'Signin' });
        }
    } else {
        // If the route does not require authentication, proceed as usual
        next();
    }
});
export default router;